.preloader {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 999999;
  background-color: #ffffff;
  background-position: center center;
  background-repeat: no-repeat;
 
  background-size: 80px;
}

.box {
  position: relative;
  width: 100%;
  height: 30vh;
  margin: 0px 0 60px 0;
  display: block;
  background: transparent;
}
.box .lines {
  position: absolute;
