  margin-bottom: 0px;
  margin-top: 0px;
  font-weight: bold;
}

.login-back {
  background-color: white;
  width: 170px;
  padding: 5px;
  position: relative;
  top: -40px;
  left: 30%;
  text-align: center;
}

.modal-login {
  background-color: rgba(222, 222, 222, 0.7);
  box-sizing: border-box;
  height: 100vh;
  padding-top: 150px;
  position: absolute;
  width: 100%;
  z-index: 2;
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);
}

.arrow {
  display: inline-block;
  padding-left: 20px;
  margin-bottom: 30px;
}

.arrow-display {
  display: -webkit-flex;
  display: flex;
}

.submit-but {
  background-color: #c84076;
  color: white;
  padding-bottom: 10px;
  width: 100%;
  border-radius: 5px;
  border-color: #c84076;
  padding: 10px;
  margin-bottom: 10px;
}

.loader,
.loader:before,
.loader:after {
  border-radius: 50%;
  width: 2em;
  height: 2em;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation: load7 1.8s infinite ease-in-out;
  animation: load7 1.8s infinite ease-in-out;
}
.loader {
  color: #c84076;
  font-size: 10px;
  margin-bottom: 50px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  text-indent: -9999em;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  content: "";
  position: absolute;
  top: 0;
}
.loader:before {
  left: -3.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader:after {
  left: 3.5em;
}
@-webkit-keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}
@keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}

.creator-hover {
  border: 2px solid #c4326d;
}

.pic {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-size: cover;
  margin-left: auto;
  margin-right: auto;
}

.pics {
  width: 60px;
  height: 60px;
  border-radius: 50%;
}

.copy-button {
  cursor: pointer;
}

.resend {
  cursor: pointer;
}

.post-image {
  width: 100%;
  height: 400px;
}

@media (max-width: 500px) {
  .landing-header {
    display: block;
  }

  .signup-button {
    text-align: center;
    margin-top: 10px;
  }
}

@media (max-width: 360px) {
  .mobile-support {
    margin-right: 20px;
  }

  .mobile-drop {
    -webkit-transform: translate3d(71px, 40px, 0px) !important;
            transform: translate3d(71px, 40px, 0px) !important;
  }
}

.disable {
  pointer-events: none;
  cursor: default;
}

@media (max-width: 768px) {
  .remove {
    display: none !important;
  }
}

.text-align-center {
  text-align: center;
  display: block;
}

