.bg-after-fluid {
  position: relative;
}
.bg-after-fluid:after {
  top: 0;
  content: "";
  border-radius: 10px;
  background: #ffe2ee;
  height: 100%;
  width: 90%;
  margin: 0 auto;
  display: block;
  position: absolute;
  left: 5%;
}
.bg-after-fluid .container {
  position: relative;
  z-index: 2;
}

.bg-gradiant-bottom:after {
  content: "";
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.01) 60%, rgba(0, 0, 0, 0.9) 100%);
  width: 100%;
  height: 250px;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
}

.bg-gradiant-top:before {
  content: "";
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.01) 60%, rgba(0, 0, 0, 0.6) 100%);
  width: 100%;
  height: 150px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.bg-lightblue-after {
  position: relative;
}
.bg-lightblue-after .container {
  position: relative;
  z-index: 3;
}
.bg-lightblue-after:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 90%;
  background-color: #fef6e7 !important;
}

.pattern-layer {
  position: relative;
}
.pattern-layer:after {
  content: "";
  position: absolute;
  top: -300px;
  right: -10%;
  height: 100%;
  z-index: 1;
  width: 120%;
  height: 100%;

  background-repeat: repeat-y;
  background-size: contain;
}
.pattern-layer:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  -webkit-backdrop-filter: blur(50px);
  backdrop-filter: blur(50px);
  background-color: rgba(255, 255, 255, 0.8);
}
.pattern-layer .layer-after {
  position: relative;
  z-index: 3;
}

.app-shape:after {
  content: "";
  position: absolute;
  width: 150px;
  height: 230px;
